body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus , input:focus {
  outline:0;
}
.App {
  text-align: center;
  background-color: #282c34; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  opacity: .2; }

.App-bg {
  background-color: #282c34;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: -100;
  position: absolute;
  width: 100%;
  top: 0; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.home-page-container {
  color: white; }

.moves-list-container {
  background-color: #132c76;
  padding: 10px;
  border-radius: 10px;
  margin: 5px; }
  .moves-list-container .move-item {
    text-align: left;
    background-color: #00004d;
    border-radius: 10px;
    margin: 5px;
    padding: 10px; }
    .moves-list-container .move-item span {
      font-weight: bold; }

.contact-page-container {
  max-width: 96%;
  margin: 0 auto; }
  .contact-page-container .list-container {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly; }
  .contact-page-container .add-btn {
    position: fixed;
    bottom: 5%;
    right: 5%;
    height: 44px;
    width: 44px;
    background-color: royalblue;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-weight: bold;
    font-size: 1.2em;
    color: white;
    text-decoration: none; }
    .contact-page-container .add-btn:hover {
      background-color: #1d44b8; }

.contact-filter {
  padding: 5px;
  border-radius: 10px;
  margin: 0 auto;
  width: 90vw; }
  .contact-filter:focus {
    outline: 0; }

.contact-preview-container {
  padding: 5px;
  margin: 5px auto;
  background-color: #fff;
  border-radius: 15px;
  width: 90vw;
  display: flex;
  text-align: left;
  color: black;
  font-weight: bold; }
  .contact-preview-container .contact-phone {
    opacity: .7;
    font-size: .8em; }
  .contact-preview-container img {
    margin-right: 10px;
    height: 50px;
    background-color: cornflowerblue;
    border-radius: 50%; }
  .contact-preview-container:hover {
    background-color: #d9d9d9;
    cursor: pointer; }

.contact-details-container {
  color: white; }
  .contact-details-container img {
    background-color: royalblue;
    border-radius: 30%;
    max-width: 100px; }
  .contact-details-container .contact-data {
    display: flex;
    align-items: center;
    justify-content: space-evenly; }
    .contact-details-container .contact-data .contact-content {
      text-align: left; }
  .contact-details-container .contact-actions button {
    background-color: royalblue;
    padding: 5px;
    border-radius: 50%;
    margin: 5px;
    height: 44px;
    width: 44px;
    border-color: #839deb; }
    .contact-details-container .contact-actions button, .contact-details-container .contact-actions button a {
      color: white;
      text-decoration: none; }

.transfer-fund-container {
  background-color: #1a3ca2;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto; }
  .transfer-fund-container h4 {
    margin: 0; }
  .transfer-fund-container input {
    background-color: transparent;
    border: 0;
    box-shadow: 0 -1px 0 0 inset rgba(0, 0, 0, 0.3); }
  .transfer-fund-container button {
    margin-left: 5px;
    border-radius: 5px;
    background-color: #fff; }

.contact-edit-container form {
  background-color: #fff;
  border-radius: 20px;
  padding: 10px; }
  .contact-edit-container form label {
    display: flex;
    justify-content: space-between;
    margin: 10px 0; }
    .contact-edit-container form label span {
      width: 100px;
      text-align: left; }
    .contact-edit-container form label input {
      flex-grow: 1;
      border-radius: 5px;
      padding: 5px; }
      .contact-edit-container form label input:focus {
        outline: 0; }
  .contact-edit-container form .btn-container {
    display: flex;
    justify-content: flex-end; }
    .contact-edit-container form .btn-container button {
      padding: 5px;
      border-radius: 3px; }
      .contact-edit-container form .btn-container button:focus {
        outline: 0; }

.signup-page-container {
  color: white; }
  .signup-page-container h3 {
    margin: 0; }
  .signup-page-container form {
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
    .signup-page-container form input:focus {
      outline: 0; }
    .signup-page-container form button {
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content; }

.charts-page-container {
  max-width: 90%;
  margin: 0 auto; }
  .charts-page-container .chart {
    background-color: black;
    padding: 20px 0 0 0; }
  .charts-page-container h1 {
    color: white;
    margin-bottom: 0; }

nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.3);
  height: 40px;
  padding-left: 0; }
  nav ul > * {
    color: white;
    text-decoration: none;
    -webkit-transition: .3s;
    transition: .3s;
    font-weight: bold;
    line-height: 40px;
    width: calc(100% / 3); }
    nav ul > *.active {
      box-shadow: 0 -3px 0 0 inset white; }
    nav ul > *:hover {
      color: #cccccc;
      box-shadow: 0 -3px 0 0 inset #cccccc; }

